const SingleClothingItem: React.FC = () => {
  // Mock data for the clothing item
  const clothingItem = {
    id: 1,
    name: "Black Wool Coat",
    image: "/api/placeholder/600/800",
    category: "Outerwear",
    season: "Winter",
    color: "Black",
    material: "Wool",
    description:
      "Classic black wool coat perfect for winter weather. Elegant and timeless design.",
    purchaseInfo: {
      store: "Zara",
      price: 120,
      dateAdded: "2024-01-15",
    },
    wearCount: 12,
    lastWorn: "2024-11-20",
    costPerWear: 10,
    careInstructions: "Dry clean only. Store on padded hanger.",
    stylingNotes:
      "Pairs beautifully with both casual jeans and formal dresses. The classic cut makes it versatile for any occasion.",
    weatherCompatibility: 85,
  };

  // Mock data for outfits containing this item
  const outfitsWithItem = [
    {
      id: 1,
      name: "Business Casual",
      images: [
        "/api/placeholder/150/200",
        "/api/placeholder/150/180",
        "/api/placeholder/150/220",
        "/api/placeholder/150/190",
      ],
      itemCount: 4,
    },
    {
      id: 2,
      name: "Winter Chic",
      images: [
        "/api/placeholder/150/210",
        "/api/placeholder/150/180",
        "/api/placeholder/150/200",
      ],
      itemCount: 3,
    },
    {
      id: 3,
      name: "Date Night",
      images: [
        "/api/placeholder/150/220",
        "/api/placeholder/150/180",
        "/api/placeholder/150/200",
        "/api/placeholder/150/190",
        "/api/placeholder/150/210",
      ],
      itemCount: 5,
    },
    {
      id: 4,
      name: "Office Meeting",
      images: [
        "/api/placeholder/150/200",
        "/api/placeholder/150/220",
        "/api/placeholder/150/180",
        "/api/placeholder/150/210",
      ],
      itemCount: 4,
    },
  ];

  // Mock data for suggested items from retailers
  const suggestedItems = [
    {
      id: 1,
      name: "Cashmere Scarf",
      image: "/api/placeholder/150/200",
      price: 85,
      retailer: "H&M",
      category: "Accessories",
    },
    {
      id: 2,
      name: "Wool Turtleneck",
      image: "/api/placeholder/150/200",
      price: 45,
      retailer: "Uniqlo",
      category: "Tops",
    },
    {
      id: 3,
      name: "Leather Gloves",
      image: "/api/placeholder/150/200",
      price: 35,
      retailer: "Zara",
      category: "Accessories",
    },
    {
      id: 4,
      name: "Ankle Boots",
      image: "/api/placeholder/150/200",
      price: 90,
      retailer: "Mango",
      category: "Shoes",
    },
    {
      id: 5,
      name: "Silk Scarf",
      image: "/api/placeholder/150/200",
      price: 65,
      retailer: "COS",
      category: "Accessories",
    },
  ];

  // Mock wear history data
  const wearHistory = [
    { date: "2024-11-20", occasion: "Work meeting" },
    { date: "2024-11-15", occasion: "Dinner out" },
    { date: "2024-11-10", occasion: "Shopping" },
    { date: "2024-11-05", occasion: "Work" },
    { date: "2024-10-28", occasion: "Date night" },
  ];

  // Masonry grid function for outfit cards
  const renderMasonryGrid = (images: string[], itemCount: number) => {
    const displayImages = images.slice(0, 4);
    const remainingCount = Math.max(0, itemCount - 4);

    if (displayImages.length === 1) {
      return (
        <div className="w-full h-full">
          <img
            src={displayImages[0]}
            alt=""
            className="w-full h-full object-cover rounded-md"
          />
        </div>
      );
    }

    if (displayImages.length === 2) {
      return (
        <div className="flex flex-col gap-2 h-full">
          <div className="h-3/5">
            <img
              src={displayImages[0]}
              alt=""
              className="w-full h-full object-cover rounded-md"
            />
          </div>
          <div className="h-2/5">
            <img
              src={displayImages[1]}
              alt=""
              className="w-full h-full object-cover rounded-md"
            />
          </div>
        </div>
      );
    }

    if (displayImages.length === 3) {
      return (
        <div className="grid grid-cols-2 gap-2 h-full">
          <div className="flex flex-col gap-2">
            <div className="h-3/5">
              <img
                src={displayImages[0]}
                alt=""
                className="w-full h-full object-cover rounded-md"
              />
            </div>
            <div className="h-2/5">
              <img
                src={displayImages[1]}
                alt=""
                className="w-full h-full object-cover rounded-md"
              />
            </div>
          </div>
          <div className="h-full">
            <img
              src={displayImages[2]}
              alt=""
              className="w-full h-full object-cover rounded-md"
            />
          </div>
        </div>
      );
    }

    return (
      <div className="grid grid-cols-3 gap-2 h-full">
        <div className="flex flex-col gap-2">
          <div className="h-2/3">
            <img
              src={displayImages[0]}
              alt=""
              className="w-full h-full object-cover rounded-md"
            />
          </div>
          <div className="h-1/3">
            <img
              src={displayImages[1]}
              alt=""
              className="w-full h-full object-cover rounded-md"
            />
          </div>
        </div>
        <div className="h-full">
          <img
            src={displayImages[2]}
            alt=""
            className="w-full h-full object-cover rounded-md"
          />
        </div>
        <div className="relative h-full">
          <img
            src={displayImages[3]}
            alt=""
            className="w-full h-full object-cover rounded-md"
          />
          {remainingCount > 0 && (
            <div className="absolute inset-0 bg-black/60 flex items-center justify-center rounded-md">
              <span className="text-white font-semibold text-sm">
                +{remainingCount}
              </span>
            </div>
          )}
        </div>
      </div>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-screen-xl mx-auto p-6">
        {/* Back button */}
        <button className="flex items-center text-gray-600 hover:text-[#1f2937] mb-8 transition-colors">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-5 w-5 mr-2"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M10 19l-7-7m0 0l7-7m-7 7h18"
            />
          </svg>
          Back to Wardrobe
        </button>

        {/* Main Content */}
        <div className="bg-white rounded-lg border border-gray-200 overflow-hidden">
          {/* Top Section - Image (60%) and Essential Details (40%) */}
          <div className="grid grid-cols-1 lg:grid-cols-5 gap-0">
            {/* Image - Takes more space */}
            <div className="lg:col-span-3 aspect-[3/4] lg:aspect-auto">
              <img
                src={clothingItem.image}
                alt={clothingItem.name}
                className="w-full h-full object-cover"
              />
            </div>

            {/* Essential Details - Takes less space */}
            <div className="lg:col-span-2 p-8">
              <div className="space-y-8">
                {/* Basic Info */}
                <div>
                  <h1 className="text-3xl font-bold text-[#1f2937] mb-6">
                    {clothingItem.name}
                  </h1>
                  <div className="space-y-4">
                    <div className="grid grid-cols-2 gap-4 text-sm">
                      <div>
                        <span className="text-gray-600 block">Category</span>
                        <p className="font-medium text-[#1f2937]">
                          {clothingItem.category}
                        </p>
                      </div>
                      <div>
                        <span className="text-gray-600 block">Season</span>
                        <p className="font-medium text-[#1f2937]">
                          {clothingItem.season}
                        </p>
                      </div>
                      <div>
                        <span className="text-gray-600 block">Color</span>
                        <p className="font-medium text-[#1f2937]">
                          {clothingItem.color}
                        </p>
                      </div>
                      <div>
                        <span className="text-gray-600 block">Material</span>
                        <p className="font-medium text-[#1f2937]">
                          {clothingItem.material}
                        </p>
                      </div>
                    </div>
                    <div>
                      <span className="text-gray-600 text-sm block mb-2">
                        Description
                      </span>
                      <p className="text-gray-800">
                        {clothingItem.description}
                      </p>
                    </div>
                  </div>
                </div>

                {/* Weather Compatibility */}
                <div>
                  <span className="text-gray-600 text-sm block mb-3">
                    Weather Compatibility
                  </span>
                  <div className="bg-gray-50 rounded-lg p-4">
                    <div className="flex items-center justify-between mb-2">
                      <span className="text-[#1f2937] font-medium">
                        Today's Score
                      </span>
                      <span className="text-2xl font-bold text-[#1f2937]">
                        {clothingItem.weatherCompatibility}%
                      </span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-2">
                      <div
                        className="bg-[#1f2937] h-2 rounded-full"
                        style={{
                          width: `${clothingItem.weatherCompatibility}%`,
                        }}
                      ></div>
                    </div>
                    <p className="text-sm text-gray-600 mt-2">
                      Perfect for today's weather conditions
                    </p>
                  </div>
                </div>

                {/* Quick Actions */}
                <div className="space-y-3">
                  <button className="w-full bg-[#1f2937] text-white py-3 rounded-md hover:bg-gray-700 transition-colors">
                    Mark as Worn Today
                  </button>
                  <button className="w-full border border-[#1f2937] text-[#1f2937] py-3 rounded-md hover:bg-[#1f2937] hover:text-white transition-colors">
                    Add to Outfit
                  </button>
                  <button className="w-full border border-gray-300 text-gray-700 py-3 rounded-md hover:border-[#1f2937] hover:text-[#1f2937] transition-colors">
                    Edit Item
                  </button>
                </div>

                {/* Usage Analytics */}
                <div className="grid grid-cols-1 gap-4">
                  <div className="text-center p-4 bg-gray-50 rounded-lg">
                    <div className="text-2xl font-bold text-[#1f2937]">
                      {clothingItem.wearCount}
                    </div>
                    <div className="text-sm text-gray-600">Times Worn</div>
                  </div>
                  <div className="text-center p-4 bg-gray-50 rounded-lg">
                    <div className="text-2xl font-bold text-[#1f2937]">
                      ${clothingItem.costPerWear}
                    </div>
                    <div className="text-sm text-gray-600">Cost per Wear</div>
                  </div>
                  <div className="text-center p-4 bg-gray-50 rounded-lg">
                    <div className="text-sm font-medium text-[#1f2937]">
                      {clothingItem.lastWorn}
                    </div>
                    <div className="text-sm text-gray-600">Last Worn</div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Content Sections */}
          <div className="p-8 space-y-12 border-t border-gray-100">
            {/* Purchase Information and Wear History - Side by Side */}
            <section>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                {/* Purchase Information */}
                <div>
                  <h2 className="text-xl font-semibold text-[#1f2937] mb-4">
                    Purchase Information
                  </h2>
                  <div className="space-y-3">
                    <div className="flex justify-between py-2 border-b border-gray-100">
                      <span className="text-gray-600">Store</span>
                      <span className="font-medium text-[#1f2937]">
                        {clothingItem.purchaseInfo.store}
                      </span>
                    </div>
                    <div className="flex justify-between py-2 border-b border-gray-100">
                      <span className="text-gray-600">Price</span>
                      <span className="font-medium text-[#1f2937]">
                        ${clothingItem.purchaseInfo.price}
                      </span>
                    </div>
                    <div className="flex justify-between py-2">
                      <span className="text-gray-600">Date Added</span>
                      <span className="font-medium text-[#1f2937]">
                        {clothingItem.purchaseInfo.dateAdded}
                      </span>
                    </div>
                  </div>
                </div>

                {/* Wear History */}
                <div>
                  <h2 className="text-xl font-semibold text-[#1f2937] mb-4">
                    Recent Wear History
                  </h2>
                  <div className="space-y-3">
                    {wearHistory.slice(0, 5).map((wear, index) => (
                      <div
                        key={index}
                        className="flex justify-between py-2 border-b border-gray-100 last:border-b-0"
                      >
                        <span className="text-gray-800">{wear.occasion}</span>
                        <span className="text-sm text-gray-600">
                          {wear.date}
                        </span>
                      </div>
                    ))}
                    <button className="text-[#1f2937] text-sm hover:underline">
                      View full history
                    </button>
                  </div>
                </div>
              </div>
            </section>

            {/* Outfits with this item - Carousel */}
            <section>
              <h2 className="text-xl font-semibold text-[#1f2937] mb-6">
                Outfits with this Item
              </h2>
              <div className="flex gap-4 overflow-x-auto pb-4 scrollbar-hide">
                {outfitsWithItem.map((outfit) => (
                  <div
                    key={outfit.id}
                    className="flex-shrink-0 w-64 bg-white rounded-lg border border-gray-200 overflow-hidden hover:shadow-lg transition-shadow cursor-pointer"
                  >
                    <div className="relative aspect-[3/4] overflow-hidden p-3">
                      <div className="w-full h-full">
                        {renderMasonryGrid(outfit.images, outfit.itemCount)}
                      </div>
                    </div>
                    <div className="p-4">
                      <h3 className="font-medium text-[#1f2937] mb-1">
                        {outfit.name}
                      </h3>
                      <p className="text-sm text-gray-600">
                        {outfit.itemCount} items
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </section>

            {/* Care Instructions and Styling Notes */}
            <section>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                <div>
                  <h2 className="text-xl font-semibold text-[#1f2937] mb-4">
                    Care Instructions
                  </h2>
                  <div className="p-4 bg-gray-50 rounded-lg">
                    <p className="text-gray-800">
                      {clothingItem.careInstructions}
                    </p>
                  </div>
                </div>

                <div>
                  <h2 className="text-xl font-semibold text-[#1f2937] mb-4">
                    Styling Notes
                  </h2>
                  <div className="p-4 bg-gray-50 rounded-lg">
                    <p className="text-gray-800">{clothingItem.stylingNotes}</p>
                    <button className="mt-3 text-[#1f2937] text-sm hover:underline">
                      Edit notes
                    </button>
                  </div>
                </div>
              </div>
            </section>

            {/* Suggested Items from Retailers - Carousel */}
            <section>
              <h2 className="text-xl font-semibold text-[#1f2937] mb-4">
                Items that go well with this
              </h2>
              <p className="text-gray-600 text-sm mb-6">
                Curated suggestions from top retailers
              </p>
              <div className="flex gap-4 overflow-x-auto pb-4 scrollbar-hide">
                {suggestedItems.map((item) => (
                  <div
                    key={item.id}
                    className="flex-shrink-0 w-48 border border-gray-200 rounded-lg overflow-hidden hover:shadow-md transition-shadow cursor-pointer"
                  >
                    <img
                      src={item.image}
                      alt={item.name}
                      className="w-full aspect-[3/4] object-cover"
                    />
                    <div className="p-3">
                      <h3 className="font-medium text-[#1f2937] text-sm mb-1">
                        {item.name}
                      </h3>
                      <p className="text-xs text-gray-600 mb-2">
                        {item.retailer} â€¢ {item.category}
                      </p>
                      <div className="flex justify-between items-center">
                        <span className="font-semibold text-[#1f2937]">
                          ${item.price}
                        </span>
                        <button className="text-xs bg-[#1f2937] text-white px-2 py-1 rounded hover:bg-gray-700 transition-colors">
                          View
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </section>
          </div>
        </div>
      </div>

      <style jsx>{`
        .scrollbar-hide {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .scrollbar-hide::-webkit-scrollbar {
          display: none;
        }
      `}</style>
    </div>
  );
};

export default SingleClothingItem;
